@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
.about {
  background: #eee;
  font-size: 1.5em;
  font-family: 'Merriweather', serif;
  line-height: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .about-p {
    width: 80%; }

/* colors */
.header {
  font-family: 'Merriweather', serif;
  letter-spacing: 1px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .header h1 {
    margin-bottom: 0; }
  .header img {
    height: 200px;
    width: 200px;
    border-radius: 50%; }
  .header__scroll {
    position: relative; }
    .header__scroll-down {
      position: absolute;
      left: 50%;
      bottom: -5vh;
      display: block;
      text-align: center;
      font-size: 20px;
      z-index: 99;
      text-decoration: none;
      text-shadow: 0;
      width: 15px;
      height: 15px;
      border-bottom: 2px solid #aaa;
      border-right: 2px solid #aaa;
      transform: translate(-50%, 0%) rotate(45deg);
      -webkit-animation: fade_move_down 2s ease-in-out infinite;
              animation: fade_move_down 2s ease-in-out infinite; }

/* colors */
.latest {
  height: 100%;
  background: #ffffff;
  margin-bottom: 2em;
  padding: 4em 2em; }
  .latest h1 {
    text-align: center; }
  .latest__card {
    padding: 1em;
    box-shadow: 3px 3px 17px rgba(0, 0, 0, 0.06);
    margin: 1em; }
    .latest__card__title {
      font-size: 1.2em;
      font-weight: 500; }
      .latest__card__title a {
        color: #1292b4; }
        .latest__card__title a:hover {
          opacity: 0.7; }
    .latest__card__date {
      font-size: 0.7em;
      color: #aaa; }

.articles {
  display: flex;
  justify-content: center;
  padding: 1em 0.5em 4em 0.5em; }

/* colors */
.card {
  width: 100%;
  padding: 1em 0.7em;
  border-radius: 0.5em;
  box-shadow: 3px 3px 17px rgba(0, 0, 0, 0.1);
  margin: 0.7em 0; }
  .card__header {
    display: flex;
    justify-content: space-between;
    padding: 1em 0;
    font-family: sans-serif; }
  .card__title {
    color: #1292b4;
    cursor: pointer; }
    .card__title h5 {
      font-size: 1.2em;
      font-weight: 400;
      margin: 0; }
    .card__title:hover {
      opacity: 0.7; }
  .card__text {
    line-height: 1.7;
    word-break: break-word; }
  .card__icon {
    display: flex;
    justify-content: center;
    align-items: center; }
    .card__icon span {
      padding-right: 0.4em; }
  .card__footer {
    font-size: 0.8em;
    color: #afafaf;
    padding: 0.5em 0; }

/* colors */
.jumbotron {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2em 1em; }
  .jumbotron__title {
    padding-top: 3em;
    font-size: 3em;
    font-weight: 400; }
  .jumbotron__text {
    color: #000;
    font-size: 1.3em;
    line-height: 1.7;
    letter-spacing: 0.01em;
    font-weight: 400; }

/* colors */
.post {
  display: flex;
  padding: 10em 2em 6em 6em; }
  @media (max-width: 599px) {
    .post {
      padding: 4em 1em; } }
  .post__info {
    margin: 0;
    color: #444; }
  .post__title {
    width: 80%;
    line-height: 1.5; }
  .post__content h1 {
    display: none;
    font-size: 3em;
    font-weight: 400;
    line-height: 1.5; }
  .post__content h1 > p {
    line-height: 1.5;
    font-style: italic; }
  .post__content figure {
    margin-left: 0; }
  .post__content figure > img {
    width: 100%; }
  .post__content p {
    line-height: 1.7;
    font-size: 1.2em;
    word-break: break-word; }
  .post__social {
    display: flex;
    justify-content: center;
    padding: 1em 0.5em; }
    .post__social__icon {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-self: center;
      height: 50px;
      width: 50px;
      border-radius: 50%;
      margin: 0 0.5em; }
      .post__social__icon img {
        height: 100%;
        width: 100%; }
      .post__social__icon:hover {
        background-color: #1292b4; }
  .post__line {
    display: block;
    padding: 0.5em 0;
    width: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1); }

